<template>
  <div class="mx-2 mt-1 mb-2">
    <div v-if="!cshow" class="div-title text-subtitle-1 mt-2 mb-1">데이터변경 쿼리 작성</div>
    <div class="d-flex justify-space-between">
      <label class="label-default"
        >SR번호/제목 : 2022101024556935 / 프로모션 종료에 따른 구분자 값에 대한 조정</label
      >
      <v-btn class="ml-1 btn-default" size="small" :rounded="5">조회</v-btn>
    </div>
    <div style="height: 216px">
      <div class="grid-default" id="cdcrGrid1"></div>
    </div>
    <v-divider class="mt-3 mb-1"></v-divider>
    <div class="d-flex">
      <div class="mr-1 w-50">
        <div class="d-flex justify-space-between">
          <label class="label-default">{{ singleQueryTitle }}</label>
          <v-btn
            icon="mdi-plus-circle"
            size="x-small"
            variant="flat"
            max-height="24"
            max-width="24"
            @click="openSingleQuery = true"
          ></v-btn>
        </div>
        <v-dialog
          :class="{ 'dialog-80': !singleQueryExpand, 'dialog-95': singleQueryExpand }"
          v-model="openSingleQuery"
          persistent
        >
          <v-card>
            <div class="div-title text-subtitle-2 d-flex justify-space-between">
              {{ singleQueryTitle }}
              <v-btn
                color="var(--titlebar-bg)"
                :icon="singleQueryExpand ? 'mdi-arrow-collapse' : 'mdi-arrow-expand'"
                size="x-small"
                variant="flat"
                max-height="24"
                max-width="24"
                @click="singleQueryExpand = !singleQueryExpand"
              ></v-btn>
            </div>
            <div>
              <textarea
                class="textarea-default"
                :rows="singleQueryExpand ? 37 : 20"
                :value="text1"
              ></textarea>
            </div>
            <div class="mb-1 mr-1 d-flex justify-end">
              <v-btn
                class="ml-1 btn-default"
                size="small"
                :rounded="5"
                @click="openSingleQuery = false"
                >취소</v-btn
              >
              <v-btn
                class="ml-1 btn-default"
                size="small"
                :rounded="5"
                @click="openSingleQuery = false"
                >작성</v-btn
              >
            </div>
          </v-card>
        </v-dialog>
        <v-spacer></v-spacer>
        <textarea class="textarea-default" rows="8" v-model="text1"></textarea>
      </div>
      <div class="mr-1 w-50">
        <div class="d-flex justify-space-between">
          <label class="label-default">{{ allQueryTitle }}</label>
          <v-btn
            icon="mdi-plus-circle"
            size="x-small"
            variant="flat"
            max-height="24"
            max-width="24"
            @click="openAllQuery = true"
          ></v-btn>
        </div>
        <v-dialog
          :class="{ 'dialog-80': !allQueryExpand, 'dialog-95': allQueryExpand }"
          v-model="openAllQuery"
          persistent
        >
          <v-card>
            <div class="div-title text-subtitle-2 d-flex justify-space-between">
              {{ allQueryTitle }}
              <v-btn
                color="var(--titlebar-bg)"
                :icon="allQueryExpand ? 'mdi-arrow-collapse' : 'mdi-arrow-expand'"
                size="x-small"
                variant="flat"
                max-height="24"
                max-width="24"
                @click="allQueryExpand = !allQueryExpand"
              ></v-btn>
            </div>
            <div>
              <textarea
                class="textarea-default"
                :rows="allQueryExpand ? 37 : 20"
                :value="text2"
              ></textarea>
            </div>
            <div class="mb-1 mr-1 d-flex justify-end">
              <v-btn
                class="ml-1 btn-default"
                size="small"
                :rounded="5"
                @click="openAllQuery = false"
                >취소</v-btn
              >
              <v-btn
                class="ml-1 btn-default"
                size="small"
                :rounded="5"
                @click="openAllQuery = false"
                >작성</v-btn
              >
            </div>
          </v-card>
        </v-dialog>
        <v-spacer></v-spacer>
        <textarea class="textarea-default" rows="8" v-model="text2"></textarea>
      </div>
    </div>
    <div class="d-flex justify-end">
      <v-btn class="ml-1 btn-default" size="small" :rounded="5">구문 확인 및 점검쿼리 생성</v-btn>
      <v-btn class="ml-1 btn-default" size="small" :rounded="5">점검쿼리 수정</v-btn>
    </div>
    <v-divider class="mt-2 mb-1"></v-divider>
    <div style="height: 200px">
      <div class="grid-default" id="cdcrGrid2"></div>
      <v-dialog
        :class="{ 'dialog-80': !openQeuryExpand, 'dialog-95': openQeuryExpand }"
        v-model="openQuery"
        persistent
      >
        <v-card>
          <div class="div-title text-subtitle-2 d-flex justify-space-between">
            {{ openQueryTitle }}
            <v-btn
              color="var(--titlebar-bg)"
              :icon="openQeuryExpand ? 'mdi-arrow-collapse' : 'mdi-arrow-expand'"
              size="x-small"
              variant="flat"
              max-height="24"
              max-width="24"
              @click="openQeuryExpand = !openQeuryExpand"
            ></v-btn>
          </div>
          <label class="label-default">UID 쿼리</label>
          <v-spacer></v-spacer>
          <div>
            <textarea
              class="textarea-default"
              :rows="openQeuryExpand ? 17 : 8"
              v-model="uidQuery"
            ></textarea>
          </div>
          <v-tabs
            density="compact"
            height="var(--subTabHeight)"
            v-model="subTab"
            center-active
            bg-color="var(--inputbar-bg)"
          >
            <v-tab value="before" selected-class="selected-tab">사전 점검 쿼리</v-tab>
            <v-tab value="after" selected-class="selected-tab">사후 점검 쿼리</v-tab>
          </v-tabs>
          <v-window v-model="subTab">
            <v-window-item value="before">
              <textarea
                class="textarea-xborder"
                :rows="openQeuryExpand ? 17 : 8"
                v-model="text3"
              ></textarea>
            </v-window-item>
            <v-window-item value="after">
              <textarea
                class="textarea-xborder"
                :rows="openQeuryExpand ? 17 : 8"
                v-model="text4"
              ></textarea>
            </v-window-item>
          </v-window>
          <v-spacer></v-spacer>
          <div class="mb-1 mr-1 d-flex justify-end">
            <v-btn class="ml-1 btn-default" size="small" :rounded="5" @click="openQuery = false"
              >취소</v-btn
            >
            <v-btn class="ml-1 btn-default" size="small" :rounded="5" @click="openQuery = false"
              >적용</v-btn
            >
          </div>
        </v-card>
      </v-dialog>
    </div>
    <div class="d-flex justify-end mt-2">
      <v-btn class="ml-1 btn-default" size="small" :rounded="5" @click="closeDialog">취소</v-btn>
      <v-btn class="ml-1 btn-default" size="small" :rounded="5" @click="closeDialog">결재</v-btn>
    </div>
  </div>
</template>
<script>
import Grid from 'tui-grid'
import GridData from '@/data.json'
import CustomUI from '@/common/customui'

export default {
  components: {},
  props: {
    show: Boolean
  },
  data() {
    return {
      cshow: this.show,
      showDialog: false,
      openSingleQuery: false,
      singleQueryExpand: false,
      singleQueryTitle: 'Single Transaction Query',
      openAllQuery: false,
      allQueryExpand: false,
      allQueryTitle: 'All Transaction Query',
      cdcrGrid1: null,
      cdcrGrid2: null,
      tab: null,
      text1:
        'UPDATE DPARP.MLCARDUD SET PROMOCODE = 0\nWHERE MEMBER_CONT = 1 AND PROMOCODE = 1;\n\nUPDATE DPARP.MLCARDUD SET PROMOCODE = 1\nWHERE MEMBER_CONT = 2 AND PROMOCODE = 2;',
      modText1: null,
      text2:
        'DELETE FROM DPARP.MLCARDUD\nWHERE RESERVED = 3;\n\nDELETE FROM DPARP.MLCARDUD\nWHERE RESERVED = 4\nAND MEMBER_CONT = 2;',
      modText2: null,
      openQuery: false,
      openQeuryExpand: false,
      openQueryTitle: '점검 쿼리 확인 및 수정',
      subTab: null,
      uidQuery: 'UPDATE DPARP.MLCARDUD SET PROMOCODE = 0\nWHERE MEMBER_CONT = 1 AND PROMOCODE = 1;',
      text3:
        'SELECT * FROM DPARP.MLCARDUD\nWHERE MEMBER_CONT = 1 AND PROMOCODE = 1\nFOR FETCH ONLY\nWITH UR;',
      text4:
        'SELECT * FROM DPARP.MLCARDUD\nWHERE MEMBER_CONT = 1 AND PROMOCODE = 1\nFOR FETCH ONLY\nWITH UR;'
    }
  },
  setup() {},
  created() {
    console.log('create : ' + this.show)
  },
  mounted() {
    console.log('mount : ' + this.show)
    this.cdcrGrid1 = CustomUI.createGrid('cdcrGrid1', true, GridData.cdcQuery1Columns)
    this.cdcrGrid1.appendRows(GridData.cdcQuery1Data)

    this.cdcrGrid2 = this.grid2 = new Grid({
      el: document.getElementById('cdcrGrid2'), // Container element
      usageStatistics: false,
      header: { height: 28, align: 'left' },
      rowHeight: 28,
      minRowHeight: 28,
      rowHeaders: ['rowNum'],
      bodyHeight: 'fitToParent',
      columns: GridData.cdcQuery2Columns,
      columnOptions: {
        resizable: true
      },
      contextMenu: () => [
        [
          {
            name: 'open',
            label: '오픈',
            action: () => {
              this.openQuery = true
            }
          }
        ]
      ]
    })
    Grid.setLanguage('ko')
    this.cdcrGrid2.appendRows(GridData.cdcQuery2Data)
  },
  unmounted() {},
  methods: {
    closeDialog() {
      this.$emit('callParent')
    }
  },
  updated() {}
}
</script>
